import React from 'react';
import { useStateValue } from "../StateProvider";
import useMovieSearch from '../useMovieSearch';
//import Response from '../response';
import Navrow from '../components/NavRow';
import Footer from '../components/Footer';

function Result() {

    const [{ term }, dispatch] = useStateValue();

    //LIVE API CALL
    const { data } = useMovieSearch(term);

    //const data = Response;

    console.log(data);

    return (
        <div>   
        <div className="tv-layout">
        <Navrow/>
        <div className="container-fluid">
            <div className="row">
            <div className="span12">
                <div className="tv-data-row">
                <h1>Search Results</h1>      
            <div className="col-12">
            <div className="search-results">
                        {term && (
                            <div >                
                                {data?.results.map(result=>(
                                    <div>
                                    <div className="search-results">
                                        <div className="item">
                                        <a href="#"><div>
                                            {
                                                result.poster_path==null?<img key='result' src={'http://image.tmdb.org/t/p/w185/vRs7MD6sTLifg12SZdWFVLVShx2.jpg'} />:
                                                <img key='result' src={`http://image.tmdb.org/t/p/w185${result.poster_path}`}/>
                                            }
                                        </div>
                                        <p key='result' className="movie-name">{result.original_title}</p></a>
                                        </div>
                                    </div>
                                    </div>
                                ))}
                            </div>
                            )}
                            
            </div></div>
            </div>
            </div>
        </div>
           </div>                                 
       
        </div>
        </div>
    )
}

export default Result
